import React, { Fragment } from 'react'
import { Button, Container, Typography, Icon } from '@material-ui/core'
import _ from 'lodash'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import { Parallax } from 'react-parallax'
import { Responsive } from 'semantic-ui-react'
import RegisterContainer from '../../containers/Register'
import ScrollDownIcon from '../../components/ScrollDownIcon'
import styles from './styles'
import { urlToKey, getWorkbook, getSheet } from 'sheetsy'
import mapSheetResToObject from '../../utils/mapSheetResToObject'

// const IMAGE =
//   'https://scontent.fbkk2-7.fna.fbcdn.net/v/t1.0-9/67597156_2331323970464406_5188439159219421184_o.jpg?_nc_cat=109&_nc_eui2=AeE8dXzDJei1c3pGImlSl_2oexmPBjKY0o1eo5bO3g1RMeVCiwnqogWD5gRUn1h0eXbBvQgoyE4-gtNdyr-yQC5enepntEYaDOO_4B-JHw_RGw&_nc_oc=AQkcJ_PGxlFlmTi61GVCvBBErbwhwH4cFqcrPpZU9w4rN8FphhFa9Brf5U3bhJad1mk&_nc_ht=scontent.fbkk2-7.fna&oh=21272498172c1ab1cef7f3d7e4f0b321&oe=5DD1265B'

const ME =
  'https://scontent.fbkk2-8.fna.fbcdn.net/v/t1.0-9/66121815_2330915116969006_5671729606877511680_n.jpg?_nc_cat=103&_nc_eui2=AeE2to6M5njuuumS8b2Z1YmMt5jsy3icALQQ52gOmzyWZxaaAT6egErjxRjRBG-yNGpcN5wa5ZoI1oY5vzdny-ANhwJE_nAsP3vMhYBi3mj__w&_nc_oc=AQkdCR1RWxfcJABqWg7nqG-I8CqLHk_IWI16w839qMhZbhb82iSgq9GHLhlCd41_H1s&_nc_ht=scontent.fbkk2-8.fna&oh=efa1c543f5753d73fa96971177910d7a&oe=5DDB4DEE'
class Index extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
  }

  state = {
    bgHeader: '',
  }

  async componentDidMount() {
    const SHEET_KEY = urlToKey(
      'https://docs.google.com/spreadsheets/d/10NpqOPf9YjVkAuWx2GmiHGpqDGx5yH3eSc42LoKXcQU/edit?usp=sharing'
    )
    // const workbook = await getWorkbook(SHEET_KEY)
    // console.log('workbook', workbook)
    // const pageInfo = _.find(workbook.sheets, sheet => sheet.name === 'page')
    // console.log('pageInfo', pageInfo)
    const sheet = await getSheet(SHEET_KEY, 'od6')
    const sheetItems = mapSheetResToObject(sheet)
    this.setState({ bgHeader: _.get(sheetItems, 'bgHeader', '') })
  }

  setShowButton = show => {
    this.setState({ showButton: show })
  }

  setShowMessage = show => {
    this.setState({ showMessage: show })
  }

  state = {
    isShowRegister: false,
  }

  handleOpenRegister = () => this.setState({ isShowRegister: true })

  handleCloseRegister = () => this.setState({ isShowRegister: false })

  BannerButton = ({ className, children, variant, color, onClick }) => {
    const { classes } = this.props
    return (
      <Button
        color={color || 'default'}
        variant={variant || 'contained'}
        aria-label="delete"
        className={classes.bannerButton}
        onClick={onClick}
      >
        {className && <Icon className={className} fontSize="default" />}
        <div className={classes.bannerButtonTextContainer}>{children}</div>
      </Button>
    )
  }

  render() {
    const { classes } = this.props
    const { isShowRegister, bgHeader } = this.state
    const { BannerButton } = this
    return (
      <>
        <RegisterContainer visible={isShowRegister} handleClose={this.handleCloseRegister} />
        <div className={classes.pageWrapper}>
          <Parallax blur={{ min: -2, max: 4 }} bgImage={bgHeader} strength={100}>
            <div className={classes.bannerColorOverlay}>
              <div className={classes.bannerContainer}>
                <Container className={classes.bannerTextContainer}>
                  <Responsive as={Fragment} {...Responsive.onlyMobile}>
                    <Typography align="center" variant="h4" color="textPrimary" paragraph>
                      มาร่วมเป็นส่วนหนึ่งกับเรา <br /> CAMT CHEERLEADER
                    </Typography>
                  </Responsive>
                  <Responsive as={Fragment} minWidth={Responsive.onlyTablet.minWidth}>
                    <Typography align="center" variant="h3" color="textPrimary" paragraph>
                      มาร่วมเป็นส่วนหนึ่งกับเรา <br /> CAMT CHEERLEADER
                    </Typography>
                  </Responsive>
                  <div className={classes.bannerButtonContainer}>
                    <BannerButton variant="outlined" className="fas fa-play">
                      ดูวิดีโอ
                    </BannerButton>
                    <BannerButton color="secondary" onClick={this.handleOpenRegister}>
                      สมัครสมาชิก
                    </BannerButton>
                  </div>
                </Container>
                <div className={classes.scrollDownContainer}>
                  <ScrollDownIcon />
                </div>
              </div>
            </div>
          </Parallax>
          <div style={{ height: '100vh' }} />
          <img src={ME} style={{ width: '100%' }} alt="ME ไง" />
        </div>
      </>
    )
  }
}

export default withStyles(styles)(Index)
